.contact {
  padding-top: 60px;
  padding-bottom: 120px;
}

.contact__form {
  display: flex;
  flex-direction: column;

  span {
    color: red;
    position: absolute;
    top: 12px;
  }

  input,
  textarea {
    padding: 15px 21px;
    width: 391px;
    background-color: #f3f3f3;
    color: #8d8d8d;
    font-family: TT Hoves;
    font-size: var(--small-size);
    font-weight: 400;
    line-height: 1.21;
    letter-spacing: 0.04em;
    text-align: left;
    border: none;
  }
}

.contact__form__label {
  margin-bottom: 10px;
}

.policy {
  font-family: TT Hoves;
  font-size: var(--small-size);
  font-weight: 400;
  line-height: 1.21;
  letter-spacing: 0.03em;
  text-align: left;
  color: var(--second-color);
  width: 391px;
  padding-left: 40px;
  margin-bottom: 21px;
}

.contact__sendForm {
  padding: 30px 47px;
  background-color: #000000;
  color: var(--main-color);
  display: flex;
  align-items: center;
  border: none;
  margin-right: auto;
  font-size: var(--smallest-size);
  line-height: 1;

  &::after {
    content: '';
    display: block;
    background-image: url(../images/arrow-right–white.svg);
    background-size: contain;
    width: 24px;
    height: 24px;
    margin-left: 5px;
  }
}

.contact__formAndPhoto {
  display: flex;
  margin-bottom: 22px;
  justify-content: space-between;
}

.contact__photo {
  width: 749px;
  display: block;
}

.contact__form__label--last {
  height: 147px;
}

.contact__form__input--last {
  height: 147px;
  resize: none;
}

.contact__form__label__phone,
.contact__form__label__email,
.contact__form__label__message {
  position: relative;
}

.phone__redstar {
  left: 128px;
}

.email__redstar {
  left: 63px;
}

.message__redstar {
  left: 96px;
}

.input__phone:valid + .phone__redstar {
  display: none;
}

.textarea:valid + .message__redstar {
  display: none;
}

.input__email:valid + .email__redstar {
  display: none;
}
